import {ModuleWithProviders, NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {OverlayModule} from '@angular/cdk/overlay';

import {AdaptFocusHolderModule} from '../focus-holder/index';
import {AdaptAlertAnimatedModule} from '../alert-animated/index';

import {AdaptModalComponent} from './modal.component';
import {AdaptDialogComponent} from './dialog.component';
import {AdaptOverModalModule} from './over-modal';
import {AdaptModalService} from './modal.service';

/**
 * Modals module
 *
 * forRoot() - initialize
 */
@NgModule({
  imports: [
    CommonModule,
    OverlayModule,
    AdaptOverModalModule,
    AdaptFocusHolderModule,
    AdaptAlertAnimatedModule
  ],
  declarations: [
    AdaptModalComponent,
    AdaptDialogComponent
  ],
  exports: [
    AdaptModalComponent,
    AdaptDialogComponent
  ],
  entryComponents: [
    AdaptModalComponent,
    AdaptDialogComponent
  ],
  providers:[AdaptModalService]
})
export class AdaptModalModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptModalModule> {
    return {
      ngModule: AdaptModalModule
    };
  }
}

export * from './modal';
export * from './modal.config';
export * from './modal.service';
export * from './dialog.component';
export * from './modal.component';
export * from './over-modal';
export * from './modal.model';
export * from './modal.animation';
